*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container{
    position: 'relative';
    display: flex;
    justify-content: center;
    align-items: center;
    width: 600px;
    height: 450px;
}

.current-image{
    border-radius: o.5rem;
    box-shadow: 0px 0px 7px #666;
    width: 100%;
    height: 100%;
}

.arrow {
    position: absolute;
    width: 2rem;
    height: 2rem;
    color: #fff;
    filter: drop-shadow(0px 0px 5px #555);
}

.arrow-left{
    left: 1rem;
}

.arrow-right{
    right: 48rem;
}

.circle-indicators{
    display: flex;
    position: absolute;
    bottom: 1rem;
}

.current-indicator{
    background-color: white;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    border: none;
    outline: none;
    margin: 0 0.2rem;
    cursor: pointer;;
}

.hide-current-image{
    display: none;
}

.inactive-indicator{
    background-color: grey;
}